<template>
<div class="contenedor">

  <SlideBar @clickCrear="crearClick" @clickActualizar="ActualizarClick" />
  <Content :valor1="crear" :valor2="actualizar" />
</div>

</template>
<script>
import SlideBar from "@/components/SlideBar.vue";
import Content from "@/components/Content.vue";

import axios from "axios";
export default {
  name: "DashBoard",
  components: {
    SlideBar,
    Content,
  },
  data(){
     return {
       crear: false,
       actualizar: false
    }
  },
  methods:{
    crearClick(){
      this.crear = true;
      this.actualizar = false;
    },
    ActualizarClick(){
      this.actualizar = true;
      this.crear = false;
      this.getTodos();
    },
    async getTodos() {
    let response = await axios.get("https://lit-reaches-52960.herokuapp.com/personas");
    console.log(response.data);
  },
  }
};
</script>

